
/* Filter Dropdown */
.filterDropdown .dropdown-toggle {
  background: #ffffff;
  color: #333;
  border: 1px solid #ccc;
  padding: 10px 15px;
  border-radius: 6px;
  transition: all 0.3s ease-in-out;
}

.filterDropdown .dropdown-toggle:hover {
  background: #ffbbbb;
  border-color: #ffd6d6;
}
